import React from 'react';
import RichTextEditor, { EditorValue, ToolbarConfig } from 'react-rte';

import Field, { FieldProps } from '../Field';

export default class RichText extends Field {
    toolbarConfig = {
        display: ['INLINE_STYLE_BUTTONS', 'BLOCK_ALIGNMENT_BUTTONS', 'BLOCK_TYPE_BUTTONS', 'LINK_BUTTONS', 'BLOCK_TYPE_DROPDOWN', 'HISTORY_BUTTONS'],
        INLINE_STYLE_BUTTONS: [
          {label: 'Bold', style: 'BOLD', className: 'custom-css-class'},
          {label: 'Italic', style: 'ITALIC'},
          {label: 'Underline', style: 'UNDERLINE'}
        ],
        BLOCK_ALIGNMENT_BUTTONS: [
            {label: 'Align Left', style: 'ALIGN_LEFT'},
            {label: 'Align Center', style: 'ALIGN_CENTER'},
            {label: 'Align Right', style: 'ALIGN_RIGHT'}
        ],
        BLOCK_TYPE_DROPDOWN: [
          {label: 'Normal', style: 'unstyled'},
          {label: 'Heading Large', style: 'header-one'},
          {label: 'Heading Medium', style: 'header-two'},
          {label: 'Heading Small', style: 'header-three'}
        ],
        BLOCK_TYPE_BUTTONS: [
          {label: 'UL', style: 'unordered-list-item'},
          {label: 'OL', style: 'ordered-list-item'}
        ]
      } as ToolbarConfig;

    constructor(props: FieldProps) {
        super(props);
        this.state = {
            isFocus: this.props.field.value ? true : false,
            editorValue: RichTextEditor.createValueFromString(this.props.field.value ? this.props.field.value : '', 'html'),
        };
    }

    onChange = (value: EditorValue) => {
        this.setState({editorValue: value});
        this.handleChanged(value.toString('html'));
    }

    renderFieldType() {
        return (
            <RichTextEditor
                value={this.state.editorValue}
                onChange={this.onChange}
                placeholder={this.props.field.placeholder}
                toolbarConfig={this.toolbarConfig}
            />
        );
    }
}